[data-theme='1'] {
    --color: #000000;
    --background: #FFFFFF;
}

[data-theme='2'] {
    --color: #000000;
    --background: #DDEEFF;
}

[data-theme='3'] {
    --color: #FFFFFF;
    --background: #212A34;
}

html {
    background-color: var(--background);
    color: var(--color);
}

.select {
    display: block;
    width: 171px;
    height: 36px;
    text-transform: capitalize;
    background-color: inherit;
    border: 1px solid inherit;
    border-radius: 2px;
    color: inherit;
    margin-top: 6px;
}
